
import XHeader from 'vux/src/components/x-header/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Group from 'vux/src/components/group/index.vue'

import Ymask from '../components/Mask'
export default {
name: 'apply',
data() {
return {
maskShow: false,
amount: 0,//订单金额
fvaBalance: '',//可用余额
widthInput: "70px",
btnMsg: "购买",
applyResult: {},
buyBackground: 'buy2',
fvaDemandAssets:0//拍活期资产
}
},
computed: {

},
components: {
XHeader,
ymask: Ymask,
XNumber,
Group
},
mounted() {
this.applyInfo();
},
methods: {
applyInfo(fundCode) {
let _this = this;
this.post({
url: "/fundTrans/queryAcctMsg/v1.0",
success: function(e) {
if (e.code == "0000") {
_this.fvaBalance = e.result.acctMsg.fvaBalance;
_this.fvaDemandAssets=e.result.acctMsg.fvaDemandAssets;
}
}
})
},
apply() {
let _this = this;
if (this.amount == 0 || this.amount > this.fvaBalance) {
return;
}
this.post({
url: "/fundTrans/buy/v1.0",
data: {
fundCode: _this.$route.query.code,
amount: _this.amount
},
success: function(e) {
if (e.code == "0000") {
let result = e.result;
_this.applyResult = result;
_this.amount=0;
_this.fvaBalance = _this.fvaBalance - result.amount;
_this.maskShow = true;
} else if (e.code == "1002" || e.code == "1005" || e.code == '1006' || e.code == '1007' ||e.code == '1008') {
_this.$vux.alert.show({
title: '提示',
content: e.msg
})
}
}
})
}
},
watch: {
amount: function(n, o) {
if (this.amount !== 0) {
this.buyBackground = 'buy1';
} else {
this.buyBackground = 'buy2';
}
if (n > this.fvaBalance) {
this.btnMsg = "余额不足";
this.buyBackground = 'buy2';
} else if(this.$route.query.code=='888888' && (n+this.fvaDemandAssets)>500000){
this.btnMsg = "超过限额";
this.buyBackground = 'buy2';
} else {
this.btnMsg = "购买"
}
}
}
}



